<template>
  <el-col :span="18" :offset="1">

    <div class="mx-boxsmall">
      <h2>charts</h2>
      <p> V-Charts </p>
      : <a target=_blank href="https://elemefe.github.io/v-charts/">https://elemefe.github.io/v-charts/</a>
      <p>,vue. package.json</p>
      <p>vcharts , </p>
    </div>

    <div class="mx-box">
      <p>:</p>
      <ve-line :data="chart1Data" :settings="chart1Settings"></ve-line>
    </div>

    <div class="mx-box">
      <p>:</p>
      <ve-histogram :data="chart1Data" :settings="chart1Settings"></ve-histogram>
    </div>

    <div class="mx-box">
      <p>:</p>
      <ve-pie :data="chart1Data" :settings="chart2Settings"></ve-pie>
    </div>

    <div class="mx-box">
      <p>:</p>
      <ve-ring :data="chart1Data" :settings="chart3Settings"></ve-ring>
    </div>

    <div class="mx-box">
      <p>:</p>
      <ve-waterfall :data="chart2Data" :settings="chart4Settings"></ve-waterfall>
    </div>

    <div class="mx-box">
      <p>:</p>
      <ve-funnel :data="chart1Data" :settings="chart5Settings"></ve-funnel>
    </div>

    <div class="mx-box">
      <p>:</p>
      <ve-radar :data="chart1Data" :settings="chart6Settings"></ve-radar>
    </div>

    <div class="mx-box">
      <p>:</p>
      <ve-map :data="chart3Data" :settings="chart7Settings"></ve-map>
    </div>

  </el-col>
</template>

<script>
import VeLine from 'v-charts/lib/line'
import VeHistogram from 'v-charts/lib/histogram'
import VePie from 'v-charts/lib/pie'
import VeRing from 'v-charts/lib/ring'
import VeFunnel from 'v-charts/lib/funnel'
import VeWaterfall from 'v-charts/lib/waterfall'
import VeRadar from 'v-charts/lib/radar'
import VeMap from 'v-charts/lib/map'

export default {
  data() {
    return {
      chart1Data: {
        columns: ['', '-1', '-2', '', ''],
        rows: [
          { '': '11', '-1': 1523, '-2': 1523, '': 0.12, '': 100 },
          { '': '12', '-1': 1223, '-2': 1523, '': 0.345, '': 100 },
          { '': '13', '-1': 2123, '-2': 1523, '': 0.7, '': 100 },
          { '': '14', '-1': 4123, '-2': 1523, '': 0.31, '': 100 },
          { '': '15', '-1': 3123, '-2': 1523, '': 0.12, '': 100 },
          { '': '16', '-1': 7123, '-2': 1523, '': 0.65, '': 100 }
        ],
      },
      chart2Data: {
        columns: ['', ''],
        rows: [
          { '': '', '': 4 },
          { '': '', '': 10 },
          { '': '', '': 5 }
        ]
      },
      chart3Data: {
        columns: ['', 'GDP'],
        rows: [
          { '': '', 'GDP': 123 },
          { '': '', 'GDP': 1223 },
          { '': '', 'GDP': 2123 },
          { '': '', 'GDP': 4123 }
        ]
      },
      chart1Settings: {
        dimension: [''],
        metrics: [ '-1', '-2', ''],
        axisSite: {
          right: ['']
        },
        yAxisType: ['KMB', 'percent'],
        yAxisName: ['', ''],
        area: true,
        stack: {
          '': ['-1', '-2']
        }
      },
      chart2Settings: {
        dimension: '',
        metrics: '-1',
        dataType: 'KMB',
        selectedMode: 'single',
        hoverAnimation: false,
        radius: 100,
        offsetY: 200
      },
      chart3Settings: {
        dimension: '',
        metrics: '-1',
        dataType: 'KMB',
        selectedMode: 'single',
        hoverAnimation: false,
        radius: [100, 80],
        offsetY: 200
      },
      chart4Settings: {
        dimension: '',
        metrics: '',
        totalNum: '24',
        dataType: 'KMB',
        totalName: '',
        remainName: ''
      },
      chart5Settings: {
        dimension: '',
        metrics: '-1',
        dataType: 'KMB',
        sequence: ['11', '12', '13', '14', '15', '16'],
        ascending: false
      },
      chart6Settings: {
        dimension: [''],
        metrics: ['-1', '-2', ''],
        dataType: { '': 'percent' }
      },
      chart7Settings: {
        position: 'china'
      },

    }
  },
  components: {
    VeLine, VeHistogram, VePie, VeRing, VeFunnel, VeWaterfall, VeRadar, VeMap
  }
}
</script>

<style>
</style>



